
import { defineComponent } from "vue";

export default defineComponent({
  props: {
    message: { type: String, default: "Delete?" },
    icon: { type: String, default: "delete" },
    text: { type: String, default: "" },
  },
  emits: ["confirm"],
  setup(props, { emit }) {
    function confirm() {
      if (window.confirm(props.message)) emit("confirm");
    }
    return { confirm };
  },
});
