<template>
  <EditorCard v-if="mphrase" icon="widgets" header="Blocks" @action="add">
    <template v-if="block" #header>
      <ArrayCopy v-model="blocks" v-model:item="block" />
      <ArrayShift v-model="blocks" :item="block" />
      <ConfirmButton @confirm="remove" />
    </template>
    <Context v-if="phrase.id" />
    <Context v-else :blocks="vblocks" :translation="phrase.context" />
    <div class="row-1 wrap block-editor">
      <div v-for="(b, i) in blocks" :key="i + '--' + Math.random()" class="row">
        <btn icon="edit" :is-on="block === b" @click="block = b" />
        <Block :ref="(el) => (el ? vblocks.push(el) : null)" :block="b" />
      </div>
    </div>
  </EditorCard>
</template>

<script lang="ts">
import ArrayCopy from "@/components/ArrayCopy.vue";
import EditorCard from "@/components/EditorCard.vue";
import ArrayShift from "@/components/ArrayShift.vue";
import ConfirmButton from "@/components/ConfirmButton.vue";
import Context from "../Context/index.vue";
import Block from "./index.vue";

import {
  computed,
  defineComponent,
  onBeforeUpdate,
  PropType,
  ref,
  toRaw,
  watch,
} from "vue";
import { Phrase, CorpusPhrase, State } from "../types";
import { newState } from "../utils";

export default defineComponent({
  name: "OrderEditor",
  components: {
    ArrayCopy,
    EditorCard,
    ArrayShift,
    ConfirmButton,
    Context,
    Block,
  },
  props: {
    modelValue: {
      type: Array as PropType<State[] | undefined>,
      default: undefined,
    },
    phrase: {
      type: Object as PropType<Phrase | CorpusPhrase>,
      default: undefined,
    },
  },
  emits: ["update:modelValue", "update:phrase"],
  setup(props, { emit }) {
    const mphrase = computed({
      get: () => props.phrase,
      set: (p) => emit("update:modelValue", p),
    });
    const blocks = computed({
      get: () => mphrase.value.blocks,
      set: (bs) => (mphrase.value.blocks = bs),
    });
    const block = computed({
      get: () => props.modelValue,
      set: (b) => emit("update:modelValue", b),
    });
    const vblocks = ref([]);
    onBeforeUpdate(() => (vblocks.value = []));

    function pickLast() {
      block.value = blocks.value
        ? blocks.value[blocks.value.length - 1]
        : undefined;
    }
    watch(blocks, pickLast, {
      immediate: true,
    });

    function add() {
      if (!blocks.value) blocks.value = [];
      blocks.value.push([newState()]);
      pickLast();
    }
    function remove() {
      if (!block.value) return;
      const index = toRaw(blocks.value).indexOf(toRaw(block.value));
      blocks.value.splice(index, 1);
      pickLast();
    }

    return { mphrase, blocks, vblocks, block, add, remove };
  },
});
</script>
